*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
#root,
#__next {
  isolation: isolate;
}
a {
  color: inherit;
  text-decoration: none;
}
ul,
ol {
  list-style: none;
}

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500&family=Roboto:wght@700;900&display=swap');

body {
  background-color: #000000;
  overflow-x: hidden;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  white-space: nowrap;
  color: #fff;
}
.wrapper {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 10px;
}
.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 15px;
}
.title {
  font-family: 'Roboto', sans-serif;
  font-size: 84.5px;
  margin-bottom: 18px;
  margin-top: 89px;
}
.subtitle {
  color: #e574bc;
  font-size: 14px;
  margin-bottom: 35px;
}
